.support {
  .search {
    margin-top: 0;
    padding-top: 76px;
    .image-wrapper {
      position: relative;
      max-width: 350px;
      margin-left: 48px;
      svg {
        position: absolute;
        &:first-of-type {
          top: 10%;
          inset-inline-start: -20%;
        }
        &:nth-of-type(2) {
          bottom: 20%;
          inset-inline-start: -100%;
        }
      }
      .item {
        display: flex;
        align-items: baseline;
        gap: 12px;
        max-width: 300px;
        padding-inline: 12px;
        position: absolute;
        bottom: 20%;
        inset-inline-end: -20%;
        font-size: var(--title-font-normal);
        background-color: var(--background-color);
        border-radius: 12px;
        box-shadow: 0px 0px 12px 0px #5b5b5b82;

        span {
          svg {
            position: static;
          }
        }
      }
    }
    .content-wrapper {
      .section-title {
        font-size: var(--title-font-lagre);
      }
      .form-search {
        position: relative;
        max-width: 370px;
        margin-top: 40px;
        padding-bottom: 48px;
        z-index: 1;
        .input-group {
          position: relative;
          svg {
            position: absolute;
            transform: translate(-50%, -50%);
            inset-inline-start: 0;
            top: 50%;
          }
          input {
            padding: 12px;
            width: 100%;
            border-radius: 12px;
            border: 1px solid var(--background-color);
            background-color: #f8f8f8;
            box-shadow: 0px 1px 8px 0px #2f2f2f29;
            outline: none;
            text-indent: 36px;
            &:focus {
              border: 1px solid #ccc;
            }
            &:active {
              border: 1px solid #ccc;
            }
          }
        }
        .search-list{
          max-height: 300px;
          overflow-y: auto;
          position: absolute;
          list-style: none;
          width: 100%;
          background-color: #ededed;
          border-radius: 0 0 12px 12px;
          border-top: 0px;
          transition: 0.5s;
          li{
            font-size: 14px;
            padding: 12px;
            border-bottom:1px solid #ccc ;
          }
        }
      }
    }
  }
  .support-question {
    padding-inline: 4%;
    padding-block: 48px;
    max-width: 1024px;
    margin-inline: auto;
    list-style: none;
    .question-item {
      margin-bottom: 24px;
      .header {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 14px;
        .icon {
          cursor: pointer;
          transition: 0.4s;
        }
        .active {
          transform: rotate(180deg);
        }
      }
      .question-description {
        overflow: hidden;
        max-height: 0;
        padding-inline: 24px;
        border-bottom: 1px solid #ccc;
        font-size: var( --title-font-descrption-two);
        padding-bottom: 4px;
        transition: 0.6s max-height;
      }
      .active {
        max-height: 250px;
      }
    }
  }
  .back-quetion{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 48px  ;
    button{
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 12px;
      border: none;
      padding: 12px 32px;
    }
  }
  .contact {
    width: 100%;
    max-width: 550px;
    margin-inline: auto;
    padding-inline: 4%;
    .form-contect {
      width: 100%;
      .input-group {
        display: flex;
        flex-direction: column;
        gap: 12px;
        margin-top: 24px;
        label {
          font-size:var( --title-font-descrption-two);
          color: #3d3d3d;
        }
        input {
          padding: 6px 12px;
          width: 100%;
          border-radius: 4px;
          border: 1px solid var(--background-color);
          background-color: #fafafa;
          box-shadow: 0px 1px 8px 0px #2f2f2f29;
          outline: none;
          font-size: var( --title-font-descrption-three);
          &:focus {
            border: 1px solid var(--main-color);
            background-color: var(--background-color);
          }
          &:active {
            border: 1px solid var(--main-color);
            background-color: var(--background-color);
          }
        }
        textarea {
          resize: none;
          height: 140px;
          padding: 6px 12px;
          width: 100%;
          border-radius: 4px;
          border: 1px solid var(--background-color);
          background-color: #fafafa;
          box-shadow: 0px 1px 8px 0px #2f2f2f29;
          outline: none;
          font-size: var( --title-font-descrption-three);

          &:focus {
            border: 1px solid var(--main-color);
            background-color: var(--background-color);
          }
          &:active {
            border: 1px solid var(--main-color);
            background-color: var(--background-color);
          }
        }
      }
      button{
        cursor: pointer;
        margin-top: 32px;
        width: 100%;
        background-color: var(--main-color);
        color: var(--background-color);
        padding: 8px 12px;
        border-radius: 6px;
        text-align: center;
        border: none;
      }
    }
  }
}
